.add-outcome-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background: var(--brand-600);
  color: white;
  border: none;
  border-radius: 999px;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: none;
}

.add-outcome-btn:hover {
  transform: none;
  background: var(--brand-900);
}

.add-outcome-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.add-outcome-btn:active {
  transform: translateY(0);
}

.add-outcome-container {
  margin-bottom: 8px;
  padding: 8px;
  background: white;
  border-radius: 6px;
  border: 1px solid #d5e5e3;
}






